import classNames from 'classnames';
import React from 'react';

import Style from './AlcoolCard.module.scss';

const AlcoolCard = (props: { className?: string; children?: React.ReactNode }): JSX.Element => {
  const { className, children } = props;
  return <div className={classNames(Style.AlcoolCard, className)}>{children}</div>;
};
AlcoolCard.defaultProps = { className: '', children: null };

export default AlcoolCard;
