import React from 'react';
import classNames from 'classnames';

import { List } from 'antd';
import IconItem from '@/components/IconItem/IconItem';

import Style from './TapStats.module.scss';

const TapStats = (props: { className?: string }): JSX.Element => {
  const { className } = props;
  const statsIcons = [
    {
      img: '/static/images/tap/token.svg',
      headingOne: 'AVAILABLE',
      headingTwo: '- ALPHA',
      headingThree: 'ALPHA ≈ - USDT',
    },
    {
      img: '/static/images/tap/fuelPump.svg',
      headingOne: 'DEPOSITS',
      headingTwo: '-',
      headingThree: 'ALPHA ≈ - USDT',
    },
    {
      img: '/static/images/tap/stack.svg',
      headingOne: 'CLAIMED',
      headingTwo: '-',
      headingThree: 'ALPHA',
    },
    {
      img: '/static/images/tap/mail.svg',
      headingOne: 'REFERRAL REWARDS',
      headingTwo: '-',
      headingThree: 'ALPHA',
    },
    {
      img: '/static/images/tap/token.svg',
      headingOne: 'MAX PAYOUT',
      headingTwo: '-',
      headingThree: 'ALPHA',
    },
    {
      img: '/static/images/tap/group.svg',
      headingOne: 'TEAM',
      headingTwo: '-/-',
      headingThree: 'PLAYERS (DIRECT/TOTAL)',
    },
  ];
  return (
    <div className={classNames(Style.TapStats, className)}>
      <List
        grid={{
          column: 3,
        }}
        dataSource={statsIcons}
        renderItem={(item) => (
          <List.Item>
            <IconItem {...item} />
          </List.Item>
        )}
      />
    </div>
  );
};

TapStats.defaultProps = { className: '' };

export default TapStats;
