import React, { useState } from 'react';
import classNames from 'classnames';

import Style from './TapAirdrop.module.scss';
import { Button } from '@/components/UI/Button';

const TABS = { TEAMVIEWER: 'Team Viewer' };

const TapAirdrop = (props: { className?: string }): JSX.Element => {
  const { className } = props;
  const [activeTab, setActiveTab] = useState(TABS.TEAMVIEWER);

  const getPage = () => {
    switch (activeTab) {
      case TABS.TEAMVIEWER:
        return (
          <div className={Style.Page}>
            <form action="">
              <label htmlFor="Player">Player</label>
              <input type="text" />
            </form>
            <Button>Show</Button>
          </div>
        );
    }
  };
  return (
    <div className={classNames(Style.TapAirdrop, className)}>
      <div className={Style.Tab}>
        {Object.values(TABS).map((item) => (
          <div
            className={classNames(Style.TabItem, { [Style.TabItemActive]: activeTab === item })}
            key={item}
            onClick={() => setActiveTab(item)}
          >
            {item}
          </div>
        ))}
      </div>
      {getPage()}
    </div>
  );
};

export default TapAirdrop;
