import React from 'react';

import Style from './AlcoolInput.module.scss';

const AlcoolInput = (props: {
  // value: string;
  label?: string;
  onSelectMax?: React.MouseEventHandler<HTMLAnchorElement>;
  // onChange: Function;
  placeholder?: string;
  postTab?: React.ReactNode;
}): JSX.Element => {
  const { placeholder, onSelectMax, postTab, label } = props;
  return (
    <div className={Style.AlcoolInput}>
      {(onSelectMax || label) && (
        <div className={Style.TitleWrap}>
          <div className={Style.Title}>{label}</div>
          {onSelectMax && <div className={Style.SelectMax}>MAX</div>}
        </div>
      )}
      <div className={Style.InputWrap}>
        <input placeholder={placeholder} type="text" />
        <div className={Style.PostTab}>{postTab}</div>
      </div>
    </div>
  );
};

AlcoolInput.propTypes = {};

export default AlcoolInput;
