import React from 'react';
import classNames from 'classnames';
import AlcoolCard from '@/components/UI/AlcoolCard/AlcoolCard';
import { Button } from '@/components/UI/Button';

import Style from './TapBuddy.module.scss';
import CommonStyle from '../../../assets/styles/common/common.module.scss';
import AlcoolInput from '@/components/UI/AlcoolInput/AlcoolInput';

const TapBuddy = (props: { className?: string }): JSX.Element => {
  const { className } = props;
  const infoList = [
    {
      label: 'Current buddy:',
      value: '-',
    },
    {
      label: 'Manager:',
      value: '-',
    },
    {
      label: 'Beneficiary:',
      value: '-',
    },
    {
      label: 'Last checkin:',
      value: '-',
    },
    {
      label: 'Inactivity threshold:',
      value: '-',
    },
    {
      label: 'Buddy:',
      value: null,
    },
  ];
  return (
    <AlcoolCard className={classNames(Style.TapBuddy, CommonStyle.CardItem, className)}>
      <h1 className={classNames(CommonStyle.CardHeadingOne)}>Get a buddy</h1>
      <div className={CommonStyle.CardInfoWrap}>
        {infoList.map((item) => (
          <div className={CommonStyle.CardInfoItem} key={item.label}>
            <div className={CommonStyle.CardInfoItemLable}>{item.label}</div>
            <div className={CommonStyle.CardInfoItemValue}>{item.value}</div>
          </div>
        ))}
      </div>
      <AlcoolInput placeholder="ADDRESS" />
      <Button className={Style.Button}>Update</Button>
      <h2 className={CommonStyle.CardHeadingThree}>Support marketing and development</h2>
    </AlcoolCard>
  );
};

TapBuddy.propTypes = {};

export default TapBuddy;
