import React from 'react';

import classNames from 'classnames';

import Style from './IconItem.module.scss';
import CommonStyle from '../../assets/styles/common/common.module.scss';

const IconItem = (props: {
  className?: string;
  img: string;
  headingOne: string;
  headingTwo?: string;
  headingThree?: string;
}): JSX.Element => {
  const { className, img, headingOne, headingTwo, headingThree } = props;
  return (
    <div className={classNames(Style.IconItem, className)}>
      <div className={classNames(Style.ImgContainer, CommonStyle.square)}>
        <img src={img} alt={headingOne} />
      </div>
      <p className={Style.HeadingOne}>{headingOne}</p>
      {headingTwo && <p className={Style.HeadingTwo}>{headingTwo}</p>}
      {headingThree && <p className={Style.HeadingThree}>{headingThree}</p>}
    </div>
  );
};

IconItem.defaultProps = { className: '', headingTwo: '', headingThree: '' };

export default IconItem;
